body {
    font-family: Calibri, Helvetica, sans-serif;
    background-color: E0FFFF;
}

.container {
    padding-left: 300px;
    padding-right: 300px;
}

input[type=text],
input[type=password],
input[type=number],
textarea {
    width: 100%;
    padding: 15px;
    margin: 5px 0 22px 0;
    display: inline-block;
    border: none;
    background: #f1f1f1;
}

input[type=text]:focus,
input[type=password],
input[type=number]:focus {
    background-color: #ccd9ff;
    outline: none;
}


