div {
    padding: 10px 0;
}

hr {
    border: 1px solid #f1f1f1;
    margin-bottom: 25px;
}

.registerbtn {
    background-color: #4d79ff;
    color: white;
    padding: 16px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 30%;
    opacity: 0.9;
    margin-left: 20%;
}

.registerbtn:hover {
    opacity: 1;
}

.resetrbtn {
    background-color: #3d3e41;
    color: white;
    padding: 16px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 30%;
    opacity: 0.9;
    margin-left: 10%;
}

.resetbtn:hover {
    opacity: 1;
}

.hrstyle {
    height: 10px;
    width: 100%;
    border-width: 0;
    color: black;
    background-color: black;
}